import React,{ Component } from 'react'
class FilterArea extends Component {
	constructor(props) {
		super(props);
		this.textInputNode=React.createRef();
	}
	render() {
		const filterLocsBySelect=this.props.filterLocsBySelect;
		const filterLocsByKeyword=this.props.filterLocsByKeyword;
		return (
			<div className="filter-area">
				<div className="filter-select">
					<label htmlFor="locationSelect"></label>
					<select id="locationSelect" 
					onChange={(e)=>{filterLocsBySelect(true,e)}}
					aria-describedby="locTypeDescription">
						<option value="all"></option>
						<option value="touristAttraction"></option>
						<option value="subwayStation"></option>
						<option value="mall"></option>
					</select>
					<div id="locTypeDescription" hidden ></div>
				</div>
				<div className="filter-input">
					<input placeholder="" 
					 id="filterText"
					ref={ this.textInputNode }
					aria-label=""></input>
					<button onClick={(e)=>{filterLocsByKeyword(this.textInputNode.current,e)}} aria-label=""></button>
				</div>
			</div>
			)
	}
}
export default FilterArea


